.navBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #007bff; /* Example background color */
  color: white;
}



.navLinks {
  display: flex;
  justify-content: flex-end; /* Keeps nav links to the right */
  list-style-type: none; /* Removes default list styling */
  margin-left: auto; /* Pushes the navLinks to the far right */
}

.navLinks a {
  color: white;
  text-decoration: none;
  padding: 0 10px; /* Adjust based on your design */
}

.navLinks a:hover {
  text-decoration: underline;
}
